import Vue from 'vue'

import App from './App.vue'

//  引入全局组件
import TypeNav from '@/components/TypeNav'
import Carousel from '@/components/Carousel'
import Pagination from '@/components/Pagination'
import { Button,MessageBox } from 'element-ui'
//  使用全局组件  第一个参数是全局组件的名字 第二个参数是哪一个组件
Vue.component(TypeNav.name,TypeNav)
Vue.component(Carousel.name,Carousel)
Vue.component(Pagination.name,Pagination)

// element-ui组件注册的第一种方式 全局注册 
Vue.component(Button.name,Button)
// 第二种方式 挂载在原型上面
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;

// 引入路由
import router from '@/router'

// 引入总仓库
import store from '@/store'

// 引入mockjs，使其运行一次，模拟数据，阻止ajax请求
import '@/mock/mockServer'

// 引入swiper插件
import 'swiper/css/swiper.css'

// 统一引入所有api
import * as API from '@/api'
// console.log(API)

// 测试api
// import {reqCategoryList} from '@/api'
// reqCategoryList()
// import {requestSearchInfo} from '@/api'
// console.log(requestSearchInfo({}))

Vue.config.productionTip = false

new Vue({
  el:'#app',
  render: h => h(App),
  beforeCreate(){
    Vue.prototype.$bus = this
    // 把API放在原型对象上面，这样就可以直接用，不用一个一个引入了
    Vue.prototype.$API = API
  },
  // 注册路由，当这书写router之后，组件身上都有$route,$router属性
  router:router,
  // 注册仓库（vuex），组件身上会多一个$store属性，存放dispatch，commit之类的
  store:store
}).$mount('#app')
