<template>
  <div class="cart">
    <h4>全部商品</h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul class="cart-list" v-for="(cart,index) in cartInfoList" :key="cart.id">
          <li class="cart-list-con1">
            <input type="checkbox" name="chk_list" @change="updateIschecked(cart,$event)" :Checked='cart.isChecked==1'>
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl">
            <div class="item-msg">{{cart.skuName}}</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{cart.cartPrice}}</span>
          </li>
          <li class="cart-list-con5">
            <a href="javascript:void(0)" class="mins" @click="handler('mins',-1,cart)">-</a>
            <input autocomplete="off" type="text" :value="cart.skuNum" minnum="1" class="itxt" @change="handler('change',$event.target.value,cart)">
            <a href="javascript:void(0)" class="plus" @click="handler('plus',1,cart)">+</a>
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{cart.skuNum * cart.skuPrice}}</span>
          </li>
          <li class="cart-list-con7">
            <a href="#none" class="sindelet" @click="removeCart(cart)">删除</a>
            <br>
            <a href="#none">移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll" type="checkbox" @change="chooseAllChecked" :checked='isAllChecked&&cartInfoList.length>0'>
        <span>全选</span>
      </div>
      <div class="option">
        <a href="#none" @click="deleteAllCheckedCart">删除选中的商品</a>
        <a href="#none">移到我的关注</a>
        <a href="#none">清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">已选择
          <span>0</span>件商品</div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">{{totalPrice}}</i>
        </div>
        <div class="sumbtn">
          <router-link class="sum-btn" to="/trade">结算</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import throttle from 'lodash/throttle'
  export default {
    name: 'ShopCart',
    mounted(){
      this.getData()
    },
    methods:{
      // 向仓库发送请求
      getData(){
        this.$store.dispatch('getCartList')
      },
      // 修改购买数量
      // mins -1  plus +1  change最终的数量，但不是变化量 提交给服务器的是当前数量和修改数量的差值
      // 比如数量为5 我要修改成10  那么提交给服务器就应该是+5  要是修改成1  那么提交给服务器就应该是-4
      // 节流，要不然连续快速点击会出现负数
      handler:throttle(async function(type,disNum,cart){
        console.log(type,disNum,cart)
        // 对情况进行分类讨论
        switch(type){
          case 'mins':
            disNum = cart.skuNum>1?-1:0
            break
          case 'plus':
            disNum = 1
            break 
          case 'change':
            // 如果非法（出现汉字或负数）
            if(isNaN(disNum) ||disNum < 1){
              disNum = 0
            }else{
              // 正常情况，可能是小数，向下取整后计算
              disNum = parseInt(disNum) - cart.skuNum
            }
            break
        }
        try {
          await this.$store.dispatch('addOrUpdataShopCart',{skuId:cart.skuId,skuNum:disNum})
          this.getData()
        } catch (error) {
          alert(error.message)  
        }
      },1000),
      // 删除购物车
      async removeCart(cart){
        try {
          await this.$store.dispatch('removeCart',cart.skuId)
          this.getData()
        } catch (error) {
          alert(error.message)  
        }
      },
      // @change="updateIschecked(cart,$event)" :Checked='cart.isChecked==1'
      // 修改产品勾选状态
      async updateIschecked(cart,event){
        try {
          // 0不勾选 1勾选 勾选为真
          // 原本没有勾选 event.target.checked的值为false 点击之后变为true 所以返回值为1
          // 上面判断，是1，就勾选
          // 带给服务器的isCheck不是布尔值，因该是1或者0
          let isChecked = event.target.checked?'1':'0'
          await this.$store.dispatch('updateChecked',{skuId:cart.skuId,isChecked:isChecked})
          console.log(isChecked)
          this.getData()
        } catch (error) {
          alert(error.message)  
        }
      },
      // 删除所有购物车
      async deleteAllCheckedCart(event){
        try {
          await this.$store.dispatch('deleteAllCheckCart',event.target.checked)
          this.getData()
        } catch (error) {
          alert(error.message)  
        }
      },
      // 全选勾选状态
      async chooseAllChecked(event){
        try {
          // 判断是否勾选，勾选为真，赋值为1，不选为假，赋值为0
          let isChecked = event.target.checked?'1':'0'
          await this.$store.dispatch('chooseAllCheck',isChecked)
          this.getData()
        } catch (error) {
          alert(error.message)  
        }
      }
    },
    computed:{
      ...mapGetters(['cartList']),
      // 获取游客购物车内容
      cartInfoList(){
        return this.cartList.cartInfoList || []
      },
      // 购物车总价格
      totalPrice(){
        let sum = 0
        this.cartInfoList.forEach(item => {
          sum += item.skuNum * item.skuPrice
        });
        return sum
      },
      // 是否勾选全部，当商品ischeck属性不全为1时，就不勾选
      isAllChecked(){
        return this.cartInfoList.every((item)=>item.isChecked==1)
      }
    }
  }
</script>

<style lang="less" scoped>
  .cart {
    width: 1200px;
    margin: 0 auto;

    h4 {
      margin: 9px 0;
      font-size: 14px;
      line-height: 21px;
    }

    .cart-main {
      .cart-th {
        background: #f5f5f5;
        border: 1px solid #ddd;
        padding: 10px;
        overflow: hidden;

        &>div {
          float: left;
        }

        .cart-th1 {
          width: 25%;

          input {
            vertical-align: middle;
          }

          span {
            vertical-align: middle;
          }
        }

        .cart-th2 {
          width: 25%;
        }

        .cart-th3,
        .cart-th4,
        .cart-th5,
        .cart-th6 {
          width: 12.5%;

        }
      }

      .cart-body {
        margin: 15px 0;
        border: 1px solid #ddd;

        .cart-list {
          padding: 10px;
          border-bottom: 1px solid #ddd;
          overflow: hidden;

          &>li {
            float: left;
          }

          .cart-list-con1 {
            width: 15%;
          }

          .cart-list-con2 {
            width: 35%;

            img {
              width: 82px;
              height: 82px;
              float: left;
            }

            .item-msg {
              float: left;
              width: 150px;
              margin: 0 10px;
              line-height: 18px;
            }
          }

          .cart-list-con4 {
            width: 10%;

          }

          .cart-list-con5 {
            width: 17%;

            .mins {
              border: 1px solid #ddd;
              border-right: 0;
              float: left;
              color: #666;
              width: 6px;
              text-align: center;
              padding: 8px;
            }

            input {
              border: 1px solid #ddd;
              width: 40px;
              height: 33px;
              float: left;
              text-align: center;
              font-size: 14px;
            }

            .plus {
              border: 1px solid #ddd;
              border-left: 0;
              float: left;
              color: #666;
              width: 6px;
              text-align: center;
              padding: 8px;
            }
          }

          .cart-list-con6 {
            width: 10%;

            .sum {
              font-size: 16px;
            }
          }

          .cart-list-con7 {
            width: 13%;

            a {
              color: #666;
            }
          }
        }
      }
    }

    .cart-tool {
      overflow: hidden;
      border: 1px solid #ddd;

      .select-all {
        padding: 10px;
        overflow: hidden;
        float: left;

        span {
          vertical-align: middle;
        }

        input {
          vertical-align: middle;
        }
      }

      .option {
        padding: 10px;
        overflow: hidden;
        float: left;

        a {
          float: left;
          padding: 0 10px;
          color: #666;
        }
      }

      .money-box {
        float: right;

        .chosed {
          line-height: 26px;
          float: left;
          padding: 0 10px;
        }

        .sumprice {
          width: 200px;
          line-height: 22px;
          float: left;
          padding: 0 10px;

          .summoney {
            color: #c81623;
            font-size: 16px;
          }
        }

        .sumbtn {
          float: right;

          a {
            display: block;
            position: relative;
            width: 96px;
            height: 52px;
            line-height: 52px;
            color: #fff;
            text-align: center;
            font-size: 18px;
            font-family: "Microsoft YaHei";
            background: #e1251b;
            overflow: hidden;
          }
        }
      }
    }
  }
</style>