<template>
  <div class="pagination">
    <!-- 左侧 -->
    <h2>
      起始{{ starAndEnd.star }} 结束{{ starAndEnd.end }} 当前页数{{
        pageNo
      }}
      总页数{{ totalPage }} 连续分页数量{{ continues }}
    </h2>
    <button :disabled="pageNo <= 1" @click="$emit('getPageNo', pageNo - 1)">
      上一页
    </button>
    <!-- 第一页，跳转到第一页 -->
    <button v-if="starAndEnd.star > 1" @click="$emit('getPageNo', 1)">1</button>
    <button v-if="starAndEnd.star > 2">···</button>

    <!-- 中间连续部分 -->
    <!-- <template v-if="isEnd == true">
      <button v-for="(page,index) in starAndEnd.end" :key="index" v-if="page>=starAndEnd.star+1" @click="$emit('getPageNo',page)">{{page}}</button>
      </template> -->
    <!-- <template v-if="isEnd == false"> -->
    <button
      v-for="(page, index) in starAndEnd.end"
      :key="index"
      v-if="page >= starAndEnd.star"
      @click="$emit('getPageNo', page)"
      :class="{ active: page == pageNo }"
    >
      {{ page }}
    </button>
    <!-- </template> -->

    <!-- 右侧 -->
    <button v-if="starAndEnd.end < totalPage - 1">···</button>
    <!-- 最后一页，跳转到totalPage（总页数）页 -->
    <button
      v-if="starAndEnd.end < totalPage"
      @click="$emit('getPageNo', totalPage)"
    >
      {{ totalPage }}
    </button>
    <button
      :disabled="pageNo >= totalPage"
      @click="$emit('getPageNo', pageNo + 1)"
    >
      下一页
    </button>

    <button style="margin-left: 30px">共 {{ totalPage }} 条</button>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  props: ["pageNo", "pageSize", "total", "continues"],
  computed: {
    isEnd() {
      if (this.pageNo > this.totalPage - Math.floor(this.continues / 2)) {
        return true;
      } else {
        return false;
      }
    },
    // 总共多少页
    totalPage() {
      return Math.ceil(this.total / this.pageSize);
    },
    // 连续页面部分逻辑
    starAndEnd() {
      const { pageNo, continues, totalPage } = this;
      // 初始换起始结束页
      let star = 0;
      let end = 0;
      // 如果总页数小于连续页数
      if (totalPage < continues) {
        (star = 1), (end = totalPage);
        // 3 4 5 6 7 100 当前1 是1 5 而不是 -1 3
        // 正常情况
      } else {
        // 起始页数等于当前页数减去连续页数除2取整
        star = pageNo - parseInt(continues / 2);
        // 结束页数等于当前页数加上连续页数除2取整
        end = pageNo + parseInt(continues / 2);
        // 如果初始计算得到初始页面小于1  比如说当前页面等于1，等于2的情况
        if (star < 1) {
          // 令初始页面为一，结束页面为持续页数
          star = 1;
          end = continues;
        } else if (end > totalPage) {
          // 结束页数大于总页数
          // 令结束页数为总页数，开始页数为总页数减去持续页数
          star = totalPage - continues;
          end = totalPage;
        }
      }
      // 返回起始结束页数
      return { star, end };
    },
    // 计算连续页面的起始与结束位置，两种情况
  },
};
</script>

<style lang="less" scoped>
.pagination {
  text-align: center;
  button {
    margin: 0 5px;
    background-color: #f4f4f5;
    color: #606266;
    outline: none;
    border-radius: 2px;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;

    &[disabled] {
      color: #c0c4cc;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background-color: #409eff;
      color: #fff;
    }
  }
}
.active {
  background-color: skyblue;
}
</style>
