<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread 就是一个小按钮带一个×的东西 类似于标签一样的-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <li class="with-x" v-if="searchParams.categoryName">
              {{ searchParams.categoryName
              }}<i @click="removeCategoryName">x</i>
            </li>
            <li class="with-x" v-if="searchParams.keyword">
              {{ searchParams.keyword }}<i @click="removeKeyword">x</i>
            </li>
            <li class="with-x" v-if="searchParams.trademark">
              {{ searchParams.trademark.split(":")[1]
              }}<i @click="removeTrademark">x</i>
            </li>
            <li
              class="with-x"
              v-for="(attr, index) in searchParams.props"
              :key="index"
            >
              {{ attr.split(":")[1] }}<i @click="removeAttr(index)">x</i>
            </li>
          </ul>
        </div>

        <!--selector 中间参数部分-->
        <SearchSelector @trademarkInfo="trademarkInfo" @attrInfo="attrInfo" />

        <!--details 底下的商品详情-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li :class="{ active: itemOne }" @click="changeOrder('1')">
                  <a
                    >综合<span
                      v-show="itemOne"
                      class="iconfont"
                      :class="{ 'icon-up': sortUp, 'icon-down': sortDown }"
                    ></span
                  ></a>
                </li>
                <li :class="{ active: itemTwo }" @click="changeOrder('2')">
                  <a
                    >价格<span
                      v-show="itemTwo"
                      class="iconfont"
                      :class="{ 'icon-up': sortUp, 'icon-down': sortDown }"
                    ></span
                  ></a>
                </li>
              </ul>
            </div>
          </div>
          <!-- 商品收获列表 -->
          <!-- 遍历是根据参数返回的数组，这里应该是根据pagesize确定返回的数量 -->
          <!-- 不同页面展示应该是因为数据库里的顺序进行筛选的，比如第一页就是数据库里的1-5，第二页就是6-10 -->
          <!-- 综合排序，价格升降排序应该就是数据库里的asc，desc这样的数据排序（MySQL） -->
          <div class="good-list">
            <ul class="yui3-g">
              <li
                class="yui3-u-1-5"
                v-for="(good, index) in goodsList"
                :key="good.id"
              >
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link :to="`/detail/${good.id}`">
                      <img :src="good.defaultImg"/>
                    </router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em> ¥ </em>
                      <i>{{ good.price }}</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a
                      target="_blank"
                      href="item.html"
                      title="促销信息，下单即赠送三个月CIBN视频会员卡！【小米电视新品4A 58 火爆预约中】"
                      >{{ good.title }}</a
                    >
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a
                      href="success-cart.html"
                      target="_blank"
                      class="sui-btn btn-bordered btn-danger"
                      >加入购物车</a
                    >
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"
                      >收藏</a
                    >
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!-- 分页 -->
          <Pagination
            :pageNo="searchParams.pageNo"
            :pageSize="searchParams.pageSize"
            :total="total"
            :continues="5"
            @getPageNo="getPageNo"
          ></Pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import SearchSelector from "./SearchSelector/SearchSelector";
export default {
  name: "Search",
  components: {
    SearchSelector,
  },
  data() {
    return {
      searchParams: {
        // 一级分类id
        category1Id: "",
        category2Id: "",
        category3Id: "",
        // 分类名字
        categoryName: "",
        // 关键字
        keyword: "",
        // 排序
        order: "1:desc",
        // 分页器当前页数
        pageNo: 1,
        // 每一页展示数据数量
        pageSize: 5,
        // 平台售卖参数属性
        props: [],
        // 品牌
        trademark: "",
      },
    };
  },
  beforeMount() {
    // 在挂在之前把当前路由的参数赋值给searchParams，传给服务器
    Object.assign(this.searchParams, this.$route.query, this.$route.params);
    // console.log(this.searchParams);
    // console.log(this.$route.query);
    // console.log(this.$route.params);
  },
  mounted() {
    this.getData();
  },
  computed: {
    ...mapGetters(["goodsList", "attrsList", "trademarkList", "total"]),

    itemOne() {
      return this.searchParams.order.indexOf("1") != -1;
    },
    itemTwo() {
      return this.searchParams.order.indexOf("2") != -1;
    },
    sortUp() {
      return this.searchParams.order.indexOf("asc") != -1;
    },
    sortDown() {
      return this.searchParams.order.indexOf("desc") != -1;
    },
  },
  methods: {
    // 把参数带给服务器
    getData() {
      this.$store.dispatch("getSearchList", this.searchParams);
    },
    // 删除通过三级菜单点击得到的面包屑
    removeCategoryName() {
      this.searchParams.categoryName = undefined;
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      this.getData(); //再次发送移除query参数的请求
      if (this.$route.params) {
        //有params参数，保留下来
        this.$router.push({ name: "search", params: this.$route.params });
      }
      console.log('removeCategoryName')
    },
    // 删除keyword面包屑
    removeKeyword() {
      (this.searchParams.keyword = undefined)
      this.getData();
      this.$bus.$emit("remove");
      if (this.$route.query) {
        this.$router.push({ name: "search", query: this.$route.query });
      }
    },
    // 自定义事件回调  @trademarkInfo='trademarkInfo'前面是自定义事件名字，后面是回调函数
    // 点击品牌进行路由跳转
    trademarkInfo(trademark) {
      // 格式是id:name
      this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;
      this.getData();
      console.log('trademarkInfo')
    },
    // 删除品牌面包屑
    removeTrademark() {
      this.searchParams.trademark = undefined;
      this.getData();
      if (this.$route.query || this.$route.params) {
        this.$router.push({
          name: "search",
          params: this.$route.params,
          query: this.$route.query,
        });
      }
      console.log('removeTrademark')
    },
    // 手机平台属性属回调函数
    // attr是一个对象，里面有id，name，value参数这些东西，attrValue是attrValueList中遍历的属性
    // attrValue遍历了很多个，每个都有点击事件，点击的那个与点击事件对应
    // 点击配置内容进行路由跳转
    attrInfo(attr, attrValue) {
      // 数据结构：['属性id':'属性值':'属性名']  后端搜索是固定写好的，所以一定要按照格式
      let props = `${attr.attrId}:${attrValue}:${attr.attrName}`;
      if (this.searchParams.props.indexOf(props) == -1) {
        this.searchParams.props.push(props);
      }
      this.getData();
      console.log('attrInfo')
    },
    // 删除配置面包屑
    removeAttr(index) {
      console.log(index);
      this.searchParams.props.splice(index, 1);
      this.getData();
      console.log('removeAttr')
    },
    // 对综合，价格进行排序
    changeOrder(index) {
      //获取初始的状态 索引 排序方法
      let originIndex = this.searchParams.order.split(":")[0]; //  1
      let originSort = this.searchParams.order.split(":")[1];  //  desc
      let newOrder = "";
      // 算法逻辑就是索引相同时取反，不同时降序
      // newOrder是params参数
      if (index === originIndex) {
        newOrder = `${originIndex}:${originSort === "desc" ? "asc" : "desc"}`;
      } else {
        originSort = "asc";
        newOrder = `${index}:${originSort}`;
      }
      // 第一次点击索引为2的按钮，就会把order的第一个参数重置为2，那么第二次点击索引为2的按钮，就是相同取反了
      // 1 2 1 相同取反 不同降序 不同降序
      // 1 2 2 相同取反 不同降序 相同取反
      this.searchParams.order = newOrder; //这里重置
      this.getData();
    },
    // 得到当前页数
    getPageNo(pageNo) {
      this.searchParams.pageNo = pageNo;
      this.getData();
      console.log(pageNo);
    },
  },
  watch: {
    // 监听路由信息，如果路由发生变化，则再次发送请求
    $route(newValue, oldValue) {
      console.log("我监听到了数据参数的变化");
      // 将1，2，3级id赋空，避免筛选错误   其他不用赋空是因为下次发送会覆盖掉上次发送的内容
      this.searchParams.category1Id = "";
      this.searchParams.category2Id = "";
      this.searchParams.category3Id = "";
      // 再次发送请求之前整理数据带给服务器
      Object.assign(this.searchParams, this.$route.query, this.$route.params);
      console.log("这是当前搜索的keyword" + this.searchParams.keyword);
      // 再次发送请求
      this.getData();
    },
  },
};
</script>

<style lang="less" scoped>
.main {
  margin: 10px 0;

  .py-container {
    width: 1200px;
    margin: 0 auto;

    .bread {
      margin-bottom: 5px;
      overflow: hidden;

      .sui-breadcrumb {
        padding: 3px 15px;
        margin: 0;
        font-weight: 400;
        border-radius: 3px;
        float: left;

        li {
          display: inline-block;
          line-height: 18px;

          a {
            color: #666;
            text-decoration: none;

            &:hover {
              color: #4cb9fc;
            }
          }
        }
      }

      .sui-tag {
        margin-top: -5px;
        list-style: none;
        font-size: 0;
        line-height: 0;
        padding: 5px 0 0;
        margin-bottom: 18px;
        float: left;

        .with-x {
          font-size: 12px;
          margin: 0 5px 5px 0;
          display: inline-block;
          overflow: hidden;
          color: #000;
          background: #f7f7f7;
          padding: 0 7px;
          height: 20px;
          line-height: 20px;
          border: 1px solid #dedede;
          white-space: nowrap;
          transition: color 400ms;
          cursor: pointer;

          i {
            margin-left: 10px;
            cursor: pointer;
            font: 400 14px tahoma;
            display: inline-block;
            height: 100%;
            vertical-align: middle;
          }

          &:hover {
            color: #28a3ef;
          }
        }
      }
    }

    .details {
      margin-bottom: 5px;

      .sui-navbar {
        overflow: visible;
        margin-bottom: 0;

        .filter {
          min-height: 40px;
          padding-right: 20px;
          background: #fbfbfb;
          border: 1px solid #e2e2e2;
          padding-left: 0;
          border-radius: 0;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);

          .sui-nav {
            position: relative;
            left: 0;
            display: block;
            float: left;
            margin: 0 10px 0 0;

            li {
              float: left;
              line-height: 18px;

              a {
                display: block;
                cursor: pointer;
                padding: 11px 15px;
                color: #777;
                text-decoration: none;
              }

              &.active {
                a {
                  background: #e1251b;
                  color: #fff;
                }
              }
            }
          }
        }
      }

      .good-list {
        margin: 20px 0;

        ul {
          display: flex;
          flex-wrap: wrap;

          li {
            height: 100%;
            width: 20%;
            margin-top: 10px;
            line-height: 28px;

            .list-wrap {
              .p-img {
                padding-left: 15px;
                width: 215px;
                height: 255px;

                a {
                  color: #666;

                  img {
                    max-width: 100%;
                    height: auto;
                    vertical-align: middle;
                  }
                }
              }

              .price {
                padding-left: 15px;
                font-size: 18px;
                color: #c81623;

                strong {
                  font-weight: 700;

                  i {
                    margin-left: -5px;
                  }
                }
              }

              .attr {
                padding-left: 15px;
                width: 85%;
                overflow: hidden;
                margin-bottom: 8px;
                min-height: 38px;
                cursor: pointer;
                line-height: 1.8;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;

                a {
                  color: #333;
                  text-decoration: none;
                }
              }

              .commit {
                padding-left: 15px;
                height: 22px;
                font-size: 13px;
                color: #a7a7a7;

                span {
                  font-weight: 700;
                  color: #646fb0;
                }
              }

              .operate {
                padding: 12px 15px;

                .sui-btn {
                  display: inline-block;
                  padding: 2px 14px;
                  box-sizing: border-box;
                  margin-bottom: 0;
                  font-size: 12px;
                  line-height: 18px;
                  text-align: center;
                  vertical-align: middle;
                  cursor: pointer;
                  border-radius: 0;
                  background-color: transparent;
                  margin-right: 15px;
                }

                .btn-bordered {
                  min-width: 85px;
                  background-color: transparent;
                  border: 1px solid #8c8c8c;
                  color: #8c8c8c;

                  &:hover {
                    border: 1px solid #666;
                    color: #fff !important;
                    background-color: #666;
                    text-decoration: none;
                  }
                }

                .btn-danger {
                  border: 1px solid #e1251b;
                  color: #e1251b;

                  &:hover {
                    border: 1px solid #e1251b;
                    background-color: #e1251b;
                    color: white !important;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
      }

      .page {
        width: 733px;
        height: 66px;
        overflow: hidden;
        float: right;

        .sui-pagination {
          margin: 18px 0;

          ul {
            margin-left: 0;
            margin-bottom: 0;
            vertical-align: middle;
            width: 490px;
            float: left;

            li {
              line-height: 18px;
              display: inline-block;

              a {
                position: relative;
                float: left;
                line-height: 18px;
                text-decoration: none;
                background-color: #fff;
                border: 1px solid #e0e9ee;
                margin-left: -1px;
                font-size: 14px;
                padding: 9px 18px;
                color: #333;
              }

              &.active {
                a {
                  background-color: #fff;
                  color: #e1251b;
                  border-color: #fff;
                  cursor: default;
                }
              }

              &.prev {
                a {
                  background-color: #fafafa;
                }
              }

              &.disabled {
                a {
                  color: #999;
                  cursor: default;
                }
              }

              &.dotted {
                span {
                  margin-left: -1px;
                  position: relative;
                  float: left;
                  line-height: 18px;
                  text-decoration: none;
                  background-color: #fff;
                  font-size: 14px;
                  border: 0;
                  padding: 9px 18px;
                  color: #333;
                }
              }

              &.next {
                a {
                  background-color: #fafafa;
                }
              }
            }
          }

          div {
            color: #333;
            font-size: 14px;
            float: right;
            width: 241px;
          }
        }
      }
    }
  }
}
</style>