<template>
  <div class="register-container">
    <!-- 注册内容 -->
    <div class="register">
      <h3>注册新用户
        <span class="go">我有账号，去 <a href="login.html" target="_blank">登陆</a>
        </span>
      </h3>
      <div class="content">
        <label>手机号:</label>
        <input type="text" placeholder="请输入你的手机号" v-model="phone">
        <span class="error-msg">错误提示信息</span>
      </div>
      <div class="content">
        <label>验证码:</label>
        <input type="text" placeholder="请输入验证码" v-model="code">
        <button class="code" @click="getCode(phone)">获取验证码</button>
        <span class="error-msg">错误提示信息</span>
      </div>
      <div class="content">
        <label>登录密码:</label>
        <input type="text" placeholder="请输入你的登录密码" v-model="password">
        <span class="error-msg">错误提示信息</span>
      </div>
      <div class="content">
        <label>确认密码:</label>
        <input type="text" placeholder="请输入确认密码" v-model="comfirmPassword">
        <span class="error-msg">错误提示信息</span>
      </div>
      <div class="controls">
        <input name="m1" type="checkbox" v-model='isCheck'>
        <span>同意协议并注册《智能商城用户协议》</span>
        <span class="error-msg">错误提示信息</span>
      </div>
      <div class="btn">
        <button @click="userRegister">完成注册</button>
      </div>
    </div>

    <!-- 底部 -->
    <div class="copyright">
      <ul>
        <li>关于我们</li>
        <li>联系我们</li>
        <li>联系客服</li>
        <li>商家入驻</li>
        <li>营销中心</li>
        <li>手机商城</li>
        <li>销售联盟</li>
        <li>智能商城社区</li>
      </ul>
      <div class="address">地址：北京市昌平区宏福科技园综合楼6层</div>
      <div class="beian">京ICP备19006430号
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Register',
    data() {
      return {
        phone:'',
        code:'',
        password:'',
        comfirmPassword:'',
        isCheck:true
      }
    },
    methods:{
      async getCode(phone){
        if(!phone) alert('请输入手机号码')
        phone && await this.$store.dispatch('getCodeNum',phone) //短路运算
        this.code = this.$store.state.user.codeNum
        alert('验证码为' + this.code)
      },
      async userRegister(){
        const {phone,password,comfirmPassword,code,isCheck} = this
        if(phone&&password&&comfirmPassword&&code&&isCheck&&password === comfirmPassword){
          await this.$store.dispatch('sendUserRegister',{phone,password,code})
          alert('注册成功，即将为您自动登录')
          await this.$store.dispatch('userLogin',{phone,password})
          console.log(phone,password)
          this.$router.push('/home')
        }else if(password !== comfirmPassword){
          alert('第二次输入密码与第一次不一致')
        }else{
          alert('请检查表单是否输入完全')
        }
      }
    }
  }
</script>

