import {v4 as uuidv4} from 'uuid'
// 单例 uuidv4的实例对象只有一个
export const getUUID = () =>{
    // 获取本地存储的uuid
    let uuid_token = localStorage.getItem('UUIDTOKEN')
    // 如果没有uuid
    if(!uuid_token){
        // 定义一个uuid
        uuid_token = uuidv4()
        // 将uuid放入本地存储
        localStorage.setItem('UUIDTOKEN',uuid_token)
    }
    // 返回uuid  
    return uuid_token
}