import { requestCartList,requestDeleteShopCart,requestUpdateIschecked } from "@/api"

const actions = {
    async getCartList({commit}){
        let result = await requestCartList()
        if(result.code === 200){
            commit('GETCARTLIST',result.data)
        }
        console.log(result)
    },
    async removeCart({commit},skuId){
        let result = await requestDeleteShopCart(skuId)
        if(result.code === 200){
            return 'ok'
        }else{
            Promise.reject(new Error('faile'))
        }
    },
    // 是否勾选
    async updateChecked({commit},{skuId,isChecked}){
        let result = await requestUpdateIschecked(skuId,isChecked)
        if(result.code === 200){
            return 'ok'
        }
    },
    // 删除选中勾选的商品
    deleteAllCheckCart({dispatch,getters}){   //从上下文（context）结构出diapatch和getters
        // 搞一个空数组 没啥用其实，不写一样可以
        let promiseAll = []
        // 对购物车里的商品进行遍历
        getters.cartList.cartInfoList.forEach(item => {
            // 筛选出已经勾选的商品
            if(item.isChecked == 1){
                // 将返回结果放进一个数组存起来
                promiseAll.push(dispatch('removeCart',item.skuId))
            }
        });
        // 如果全部成功，那么返回成功，只要有一个失败，那么返回失败  那边在等着 如果成功 进入try 失败进入catch
        return Promise.all(promiseAll)
    },
    // 全选勾选按钮
    chooseAllCheck({dispatch,getters},isChecked){
        // 搞一个空数组 没啥用其实，不写一样可以
        let promiseAll = []
        // 对购物车里的商品进行遍历
        getters.cartList.cartInfoList.forEach(item => {
            // 将返回结果放进一个数组存起来  按照更新的参数对其进行赋值
            promiseAll.push(dispatch('updateChecked',{skuId:item.skuId,isChecked}))
        })
        // 如果全部成功，那么返回成功，只要有一个失败，那么返回失败  那边在等着 如果成功 进入try 失败进入catch
        return Promise.all(promiseAll)
    }
}
const mutations = {
    GETCARTLIST(state,value){
        state.cartList = value || []
    }
}
const state = {
    cartList:[]
}
const getters = {
    cartList(state){
        return state.cartList[0] || {}
    }
}
export default {
    actions,
    mutations,
    state,
    getters
}