import Vue from 'vue'

import VueRouter from 'vue-router'

import Home from '@/pages/Home'

import Login from '@/pages/Login'

import Search from '@/pages/Search'

import Register from '@/pages/Register'

import Detail from '@/pages/Detail'

import AddCartSuccess from '@/pages/AddCartSuccess'

import ShopCart from '@/pages/ShopCart'

import Trade from '@/pages/Trade'

import Pay from '@/pages/Pay'

import PaySuccess from '@/pages/PaySuccess'

import Center from '@/pages/Center'

// 路由判断需要用到仓库存的Token
import store from '@/store'

// 二级路由
import MyOrder from '@/pages/Center/myOrder'

import GroupOrder from '@/pages/Center/groupOrder'

Vue.use(VueRouter)

// 重写push和replace方法，从而使编程式路由跳转到当前路由（参数不变），不会出现NavigationDuplicated的警告错误
// 备份原有的push和replace方法  原有的方法是在构造函数的实例对象上
let originPush = VueRouter.prototype.push
let originReplace = VueRouter.prototype.replace

// 重写方法  为什么会打印呢  因为默认就是push方法
VueRouter.prototype.push = function (location, resolve, reject) {
    if (resolve && reject) {
        originPush.call(this, location, resolve, reject)
        // console.log(this)
    } else {
        originPush.call(this, location, () => { }, () => { })
        // console.log(this)
    }
}

VueRouter.prototype.replace = function (location, resolve, reject) {
    if (resolve && reject) {
        originReplace.call(this, location, resolve, reject)
        // console.log(this)
    } else {
        originReplace.call(this, location, () => { }, () => { })
        // console.log(this)
    }
}

let router = new VueRouter({
    routes: [
        {
            name: 'center',
            path: '/center',
            component: Center,
            redirect:'center/myOrder',
            children: [
                {
                    name: 'myOrder',
                    path: 'myOrder',
                    component: MyOrder
                },
                {
                    name: 'groupOrder',
                    path: 'groupOrder',
                    component: GroupOrder,
                }
            ]
        },
        {
            name: 'paysuccess',
            path: '/paysuccess',
            component: PaySuccess
        },
        {
            name: 'pay',
            path: '/pay',
            component: Pay
        },
        {
            name: 'trade',
            path: '/trade',
            component: Trade,
        },
        {
            name: 'shopcart',
            path: '/shopcart',
            component: ShopCart,
        },
        {
            name: 'addcartsuccess',
            path: '/addcartsuccess',
            component: AddCartSuccess,
        },
        {
            path: '/detail/:goodId?',
            component: Detail,
            meta: {
                isShow: true
            }
        },
        {
            path: '/home',
            component: Home,
            meta: {
                isShow: true
            }
        },
        {
            path: '/login',
            component: Login,
            meta: {
                isShow: false
            }
        },
        {
            name: 'search',
            path: '/search/:keyword?', //params参数
            component: Search,
            meta: {
                isShow: true
            }
        },
        {
            path: '/register',
            component: Register,
            meta: {
                isShow: false
            }
        },
        // 重定向默认是/home，进入时就是home
        {
            path: '*',
            redirect: '/home'
        }

    ],
    // 让路由跳转之后，定位到页面最上端
    scrollBehavior(to, from, savePosition) {
        return { y: 0 }
    }
})

// 暴露前设置路由守卫
// 全局前置守卫  在路由跳转前进行判断
router.beforeEach(async (to, from, next) => {
    let token = store.state.user.token
    let name = store.state.user.userInfoList.name
    // // 用户已经登陆，还想去login页面，不行
    // token&&to.path=='/login'&&next('/home')
    // // 放行
    // next()
    // // 很奇怪的逻辑
    if (token) {
        if (to.path == '/login') {
            next('/home')
        } else {
            // 访问非登陆注册页面
            // 有name放行
            if (name) {  //解决有token但是未登录情况
                next()
            } else {   // 没有就发请求获取用户信息
                try {
                    await store.dispatch('getUserInfo')
                    next()
                } catch (error) {
                    // 如果失败就退出 一般是token过期了
                    store.dispatch('userLogout')
                    // 然后进入登陆页面 重新登录获取最新token
                    next('/login')
                }
            }
        }
    } else {
        // 未登录 不能去交易相关，不能去支付相关【pay，paysuccess】，不能去个人中心
        // 提示后转至转至登录页面
        let toPath = to.path
        console.log(toPath)
        if(toPath.indexOf('/trade')!=-1||toPath.indexOf('/pay')!=-1||toPath.indexOf('/center')!=-1||toPath.indexOf('/shop')!=-1){
            next('login?redirect='+toPath)
            // 把去但又未去成的路由保存在地址栏中
        }
        else{
            next()
        }
    }
})

export default router