<template>
    <div class="type-nav">
            <div class="container">
                <div @mouseenter="enterNav" @mouseleave="leaveNav">
                    <h2 class="all">全部商品分类</h2>
                    <!-- 三级联动 -->
                    <transition name="sort">
                        <div v-show="isShow" class="sort">
                        <div class="all-sort-list2" @click="goSearch">
                            <div class="item" v-for="(c1,index) in categoryList" :key="c1.categoryid" >
                                <h3 @mouseenter="indexInter(index)" :class="{current:currentIndex==index,animate__bounceIn:currentIndex==index}">  
                                    <!-- 不传参默认参数是事件  当索引值和当前索引值一样，就修改样式 -->
                                    <a :data-categoryName='c1.categoryName' :data-category1Id='c1.categoryId'>{{c1.categoryName}}</a>
                                </h3>
                                <!-- 二级，三级分类 -->
                                <!-- 通过js实现是否展示二三级联动 -->
                                <div class="item-list clearfix" :style="{display:currentIndex==index?'block':'none'}">
                                    <div class="subitem">
                                        <dl class="fore" v-for="c2 in c1.categoryChild" :key="c2.categoryId">
                                            <dt>
                                                <a :data-categoryName='c2.categoryName' :data-category2Id='c2.categoryId'>{{c2.categoryName}}</a>
                                            </dt>
                                            <dd>
                                                <em v-for="c3 in c2.categoryChild" :key="c3.categoryId">
                                                    <a :data-categoryName='c3.categoryName' :data-category3Id='c3.categoryId'>{{c3.categoryName}}</a>
                                                </em>
                                            </dd>
                                        </dl>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div> 
                    </transition>
                </div>
                <nav class="nav">
                    <a href="###">服装城</a>
                    <a href="###">美妆馆</a>
                    <a href="###">智能超市</a>
                    <a href="###">全球购</a>
                    <a href="###">闪购</a>
                    <a href="###">团购</a>
                    <a href="###">有趣</a>
                    <a href="###">秒杀</a>
                </nav>
            </div>
        </div>
</template>

<script>
import { mapState } from 'vuex'
import throttle from 'lodash/throttle'
export default {
    name:'TypeNav',
    data() {
        return {
            currentIndex:-1,  //修改样式
            isShow:true       //修改是否展示typeNav
        }
    },
    methods:{
        indexInter:throttle(function(index){  //节流,50ms只能执行一次
            console.log(index)
            this.currentIndex = index
        },50),
        enterNav(){
            if(this.$route.path !== '/home'){   //如果路径不是home，那么挂载的时候将isShow改成false
            this.isShow = true
            }
        },
        leaveNav(){
            this.currentIndex = -1                //离开隐藏样式
            if(this.$route.path !== '/home'){   //如果路径不是home，那么挂载的时候将isShow改成false
            this.isShow = false
            }
        },
        goSearch(event){
            // 编程式路由+事件委派解决声明式路由卡顿的现象
            // 存在问题：
            // 事件委派，是把全部子节点的事件委派给父节点
            // 点击a标签的时候，对a进行路由跳转，但是怎么确定一定是a标签呢，不是dl，dt，不是其他的a标签
            // 解决方法
            // 把a节点加上自定义data-categoryName属性，其他子节点是没有的
            let element = event.target
            // 获取当前出发事件的节点，有data-categoryName的属性的节点一定是a标签
            // 节点有一个属性式dataset，可以获取节点的自定义属性与属性值（属性名浏览器会自己变成小写）
            console.log(element.dataset)
            let{categoryname,category1id,category2id,category3id} = element.dataset
            // 这里式自定义属性 自定义属性统一用 :data-xxxx:'xxxx'来定义，命名时用驼峰命名，但vue会自动变为小写，所以使用时要使用小写
            if(categoryname){
                let location = {name:'search'}
                let query = {categoryName:categoryname}
                if(category1id){
                    query.category1id = category1id
                }else if(category2id){
                    query.category2id = category2id
                }else{
                    query.category3id = category3id
                }
                // 如果由params参数，那么也带过去
                if(this.$route.params){
                    location.params = this.$route.params
                }
                console.log(query)
                location.query = query
                this.$router.push(location)
            }
        },
    },
    computed:{
        ...mapState({
            // 对象形式，其value值应该是一个函数，当使用到该计算属性时，会立即执行该函数并返回值
            categoryList:(state)=>state.home.categoryList
        })
    },
    mounted(){
        if(this.$route.path !== '/home'){   //如果路径不是home，那么挂载的时候将isShow改成false
            this.isShow = false
        }
    }
}
</script>

<style scoped lang="less">
    .type-nav {
        border-bottom: 2px solid #e1251b;

        .container {
            width: 1200px;
            margin: 0 auto;
            display: flex;
            position: relative;

            .all {
                width: 210px;
                height: 45px;
                background-color: #e1251b;
                line-height: 45px;
                text-align: center;
                color: #fff;
                font-size: 14px;
                font-weight: bold;
            }

            .nav {
                a {
                    height: 45px;
                    margin: 0 22px;
                    line-height: 45px;
                    font-size: 16px;
                    color: #333;
                }
            }

            .sort {
                position: absolute;
                left: 0;
                top: 45px;
                width: 210px;
                height: 461px;
                position: absolute;
                background: #fafafa;
                z-index: 999;

                .all-sort-list2 {
                    .item {
                        h3 {
                            line-height: 30px;
                            font-size: 14px;
                            font-weight: 400;
                            overflow: hidden;
                            padding: 0 20px;
                            margin: 0;

                            a {
                                color: #333;
                            }
                        }

                        .item-list {
                            display: none;
                            position: absolute;
                            width: 734px;
                            min-height: 460px;
                            background: #f7f7f7;
                            left: 210px;
                            border: 1px solid #ddd;
                            top: 0px;
                            z-index: 9999 !important;

                            .subitem {
                                float: left;
                                width: 650px;
                                padding: 0 4px 0 8px;

                                dl {
                                    border-top: 1px solid #eee;
                                    padding: 6px 0;
                                    overflow: hidden;
                                    zoom: 1;

                                    &.fore {
                                        border-top: 0;
                                    }

                                    dt {
                                        float: left;
                                        width: 54px;
                                        line-height: 22px;
                                        text-align: right;
                                        padding: 3px 6px 0 0;
                                        font-weight: 700;
                                    }

                                    dd {
                                        float: left;
                                        width: 415px;
                                        padding: 3px 0 0;
                                        overflow: hidden;

                                        em {
                                            float: left;
                                            height: 14px;
                                            line-height: 14px;
                                            padding: 0 8px;
                                            margin-top: 5px;
                                            border-left: 1px solid #ccc;
                                        }
                                    }
                                }
                            }
                        }

                        // &:hover {
                        //     .item-list {
                        //         display: block;
                        //     }
                        // }
                    }
                    .current {
                            background-color: skyblue;
                    }
                }
            }
            //进入动画，也可以设置离开动画，格式都是一样的  
            .sort-enter{
                height: 0px;
                opacity: 0;
            }
            .sort-enter-to{
                height: 461px;
                opacity: 100%;
            }
            // 动画持续时间，速率等等
            .sort-enter-active{
                transition: 0.5s linear ;
            }
        }
    }
</style>