// 对api进行统一管理
import requests from './request'
import mockRequests from './mockAjax'

// 三级联动接口 /api/product/getBaseCategoryList get请求 无参数
// 使用axios函数的方式发送请求
export const reqCategoryList = function(){
    // console.log(requests)
    return requests({url:'/product/getBaseCategoryList',method:'get'})
}
export const requestBannerList = function(){
    // console.log(mockRequests)
    return mockRequests({url:'/banner.json',method:'get'}) //因为baseurl已经写好mock了，这里直接用banner就好了
}

export const requestFloorList = function(){
    return mockRequests({url:'/floor.json',method:'get'})
}

// 获取搜索模块数据  请求地址：/api 请求方式post 需要携带参数
// 当前接口获取服务器搜索模块的数据，需要给服务器传递一个参数，至少是一个空对象，否则收不到信息
export const requestSearchInfo = function(params){
    return requests({url:'/list',method:'post',data:params})
}

// 获取detail模块产品详细信息数据  请求地址/api/item/{ skuId }
export const requestDetailList = function(skuId){
    return requests({url:`/item/${skuId}`,method:'get'})
}

// 添加购物车接口 请求地址/api/cart/addToCart/{ skuId }/{ skuNum }  post  通过userTempId给游客建了一个表
// 从购物车这里开始所有请求都加了一个请求头信息userTempId，根据请求该信息确定游客身份然后返回该游客购物车的数据
export const reqAddOrUpdataShopCart = function(skuId,skuNum){
    return requests({url:`/cart/addToCart/${skuId}/${skuNum}`,method:'post'})
}
// 获取购物车列表数据接口 get
export const requestCartList = function(){
    return requests({url:'/cart/cartList',method:'get'})
}

// 删除购物车接口 /api/cart/deleteCart/{skuId} delete
export const requestDeleteShopCart = function(skuId){
    return requests({url:`/cart/deleteCart/${skuId}`,method:'delete'})
}   

// 购物车产品状态接口 /api/cart/checkCart/{skuId}/{isChecked} get
export const  requestUpdateIschecked = function(skuId,isChecked){
    return requests({url:`/cart/checkCart/${skuId}/${isChecked}`,method:'get'})
}

// 获取验证码接口  /api/user/passport/sendCode/{phone}  get
export const requestGetCode = function(phone){
    return requests({url:`/user/passport/sendCode/${phone}`,method:'get'})
}

// 注册用户地址  /api/user/passport/register  phone,password,code params参数
export const requestUserRegister = function(data){
    return requests({url:`/user/passport/register`,data,method:'post'})
}

// 用户登录接口 /api/user/passport/login  phone,password  post
export const requestUserLogin = function(data){
    return requests({url:`/user/passport/login`,data,method:'post'})
}

// 获取拥堵登录信息 /api/user/passport/auth/getUserInfo  get
export const requsetGetUserInfo = function(){
    return requests({url:`/user/passport/auth/getUserInfo`,method:'get'})
}

// 退出登录接口  /api/user/passport/logout get
export const requsetUserLogout = function(){
    return requests({url:`/user/passport/logout`})
}

// 获取用户地址信息  /api/user/userAddress/auth/findUserAddressList  get
export const requestUserAddressList = function(){
    return mockRequests({url:'/trade.json',method:'get'})
}

// 获取订单页详情  /api/order/auth/trade  get
export const requsetOrderInfo = function(){
    return requests({url:`/order/auth/trade`,method:'get'})
}

// 提交订单接口 /api/order/auth/submitOrder?tradeNo={tradeNo} post
// 路由传参才需要占位符
export const requsetSubmitOrder = function(tradeNo,data){
    return requests({url:`/order/auth/submitOrder?tradeNo=${tradeNo}`,data,method:'post'})
}

// 获取订单支付信息 /api/payment/weixin/createNative/{orderId} get
export const requestPayInfo = function(orderId){
    return requests({url:`/payment/weixin/createNative/${orderId}`,method:'get'})
}

// 获取订单支付状态  /api/payment/weixin/queryPayStatus/{orderId} get
export const requestPayStatus = function(orderId){
    return requests({url:`/payment/weixin/queryPayStatus/${orderId}`,method:'get'})
}

// 获取我的订单列表   /api/order/auth/{page}/{limit}   get
export const requestGetMyOrder = function(page,limit){
    return requests({url:`/order/auth/${page}/${limit}`,method:'get'})
}