//引入mockjs模块
import Mock from 'mockjs'

// 将json格式数据引进来
// 一般来说都是先暴露再引入，但是图片和json格式数据是默认对外暴露的，所以不需要对json数据进行暴露
import banner from './banner.json'
import floor from './floor.json'
import trade from './trade.json'

// mock数据 第一个参数是请求地址 第二个参数是请求数据
Mock.mock('/mock/banner.json',{code:200,data:banner})
Mock.mock('/mock/floor.json',{code:200,data:floor})
Mock.mock('/mock/trade.json',{code:200,data:trade})