// 对axios进行二次封装
import store from '@/store'
import axios from 'axios'

// 引入进度条
import nProgress from 'nprogress'
// 引入进度条样式
import 'nprogress/nprogress.css'
// 创建axios实例
const requsets = axios.create({
    baseURL:"http://gmall-h5-api.atguigu.cn/api",
})

export default requsets

// 请求拦截器
requsets.interceptors.request.use((config)=>{
    // config：配置对象 里面有一个属性很重要 headers请求头
    if(store.state.detail.uuid_token){
        // 这个字段是和后端人员商量好的
        config.headers.userTempId = store.state.detail.uuid_token
    }
    // 配置token，通过token来确定用户
    if(store.state.user.token){
        config.headers.token = store.state.user.token
    }
    nProgress.start()
    return config
})

// 响应拦截器
requsets.interceptors.response.use((respone)=>{
    // 相应成功，返回服务器数据，服务器返回数据后，相应拦截器可以检测的到，可以去做一些事情
    nProgress.done()
    return respone.data
},(error)=>{
    // 响应失败的回调函数，中止promise
    return new Promise.reject(new error('error'))
})