import { requestSearchInfo } from "@/api"
// 书写逻辑，也可以处理异步任务
const actions = {
    // 这里可以书写业务逻辑，但是不能修改state 这里一般用小写
    async getSearchList({commit},params={}){
        let result = await requestSearchInfo(params)
        // requestSearchInfo函数在调用获取服务器数据时，至少传递一个参数
        // params参数：当用户派发action时，是第二个参数传过来的，至少是一个空对象
        if(result.code === 200){
            commit('GETSEARCHLIST',result.data)
            // console.log(result.data)
        }
    }
}
// 对数据进行修改
const mutations = {
    GETSEARCHLIST(state,value){
        state.searchList = value
    }
}
// 存储数据的地方
const state = {
    searchList:{}
}
// 对数据进行加工，可以理解成计算属性，为简化数据而生
const getters = {
    goodsList(state){
        // 如果服务器数据回来了就返回数据，如果没有就返回一个空数组
        // 假如网络不给力，返回的应该时underfined,underfined是不可以遍历的，遍历会报错，空数组遍历至少不会报错
        // 所以说新的计算属性至少是一个数组，以后可能会出现这种问题，要大概有个了解
        return state.searchList.goodsList || []
    },
    attrsList(state){
        return state.searchList.attrsList || []
    },
    trademarkList(state){
        return state.searchList.trademarkList || []
    },
    total:function(state){
        return state.searchList.total
    }
}

export default {
    actions,
    mutations,
    state,
    getters
}