import { reqCategoryList, requestBannerList, requestFloorList } from "@/api"

// 书写逻辑，也可以处理异步任务
const actions = {
    // 设置传递过来的行为  这里可以书写业务逻辑，但是不能修改state 这里一般用小写
    async categoryList({commit}){
        let result = await reqCategoryList()
        if(result.code === 200){
            commit('CATEGORYLIST',result.data)
        }
    },
    async getBannerList({commit}){
        let result = await requestBannerList()
        console.log(result)
        if(result.code === 200){
            commit('GETBANNERLIST',result.data)
        }
    },
    async getFloorList({commit}){
        let result = await requestFloorList()
        if(result.code === 200){
            commit('GETFLOORLIST',result.data)
        }
    }
}
// 对数据进行修改
const mutations = {
    CATEGORYLIST(state,value){
        state.categoryList = value.splice(0,15)
    },
    GETBANNERLIST(state,value){
        state.bannerList = value
    },
    GETFLOORLIST(state,value){
        state.floorList = value
        console.log(state.floorList)
    }
}
// 存储数据的地方
const state = {
    categoryList:[],
    bannerList:[],
    floorList:[]
}
// 对数据进行加工，可以理解成计算属性
const getters = {}

export default {
    actions,
    mutations,
    state,
    getters
}