<template>
    <header class="header">
        <!-- 头部的第一行 -->
        <div class="top">
            <div class="container">
                <div class="loginList">
                    <p>智能商城欢迎您！</p>
                    <p v-if="!userName">
                        <span>请</span>
                        <router-link to="/login">登录</router-link>
                        <router-link to="/register" class="register">免费注册</router-link>
                    </p>
                    <p v-else>
                        <a>{{userName}}</a>
                        <a class="register" @click="logout">退出登录</a>
                    </p>
                </div>
                <div class="typeList">
                    <router-link to="/center">我的订单</router-link>
                    <router-link to="/shopCart">我的购物车</router-link>
                    <a href="###">我的商城</a>
                    <a href="###">智能会员</a>
                    <a href="###">企业采购</a>
                    <a href="###">关注智能商城</a>
                    <a href="###">合作招商</a>
                    <a href="###">商家后台</a>
                </div>
            </div>
        </div>
        <!--头部第二行 搜索区域-->
        <div class="bottom">
            <h1 class="logoArea">
                <router-link to="/home" class="logo" >
                    <img src="./images/logo1.png">
                </router-link>
            </h1>
            <div class="searchArea">
                <form action="###" class="searchForm">
                    <input type="text" v-model="keyword" id="autocomplete" class="input-error input-xxlarge" />
                    <button class="sui-btn btn-xlarge btn-danger" type="button" @click="goSearch">搜索</button>
                </form>
            </div>
        </div>
    </header>
</template>

<script>
export default {
    name:'Header',
    data() {
        return {
            keyword:''
        }
    },
    methods:{
        // 编程式路由
        goSearch(){
            // 第一种字符串形式
            // this.$router.push('/search/' + this.keyword + '?k=' + this.keyword.toUpperCase())
            // 第二种模板字符串形式
            // this.$router.push(`/search/${this.keyword}?k=${this.keyword.toUpperCase()}`)
            // 第三种对象形式  如果有params参数一定要配置name（命名路由），不能使用path来当作路径
            let location = {name:'search',params:{keyword:this.keyword || undefined}}
            // 如果由query参数，那么把query参数也带过去s
            if(this.$route.query){
                location.query = this.$route.query
            }
            this.$router.push(location)
        },
        // 用户退出登录
        async logout(){
            try {
                await this.$store.dispatch('userLogout')
                this.$router.push('/home')
            } catch (error) {
                alert(error.message)   
            }
        }
    },
    mounted(){
        // 删除keyword面包屑  事件总线
        this.$bus.$on('remove',()=>{
            this.keyword = undefined
        })
    },
    computed:{
        userName(){
            return this.$store.state.user.userInfoList.name
        },
        // token(){
        //     return this.$store.state.user.token
        // }
    },
    // watch:{
    //     token(){
    //         // 获取用户信息  发请求  收到数据后header组件需要使用
    //         this.$store.dispatch('getUserInfo')
    //     }
    // }
}
</script>

<style scope lang='less'>
    .header {
        &>.top {
            background-color: #eaeaea;
            height: 30px;
            line-height: 30px;

            .container {
                width: 1200px;
                margin: 0 auto;
                overflow: hidden;

                .loginList {
                    float: left;

                    p {
                        float: left;
                        margin-right: 10px;

                        .register {
                            border-left: 1px solid #b3aeae;
                            padding: 0 5px;
                            margin-left: 5px;
                        }
                    }
                }

                .typeList {
                    float: right;

                    a {
                        padding: 0 10px;

                        &+a {
                            border-left: 1px solid #b3aeae;
                        }
                    }

                }

            }
        }

        &>.bottom {
            width: 1200px;
            margin: 0 auto;
            overflow: hidden;

            .logoArea {
                float: left;

                .logo {
                    img {
                        width: 175px;
                        margin: 25px 45px;
                    }
                }
            }

            .searchArea {
                float: right;
                margin-top: 35px;

                .searchForm {
                    overflow: hidden;

                    input {
                        box-sizing: border-box;
                        width: 490px;
                        height: 32px;
                        padding: 0px 4px;
                        border: 2px solid #ea4a36;
                        float: left;

                        &:focus {
                            outline: none;
                        }
                    }

                    button {
                        height: 32px;
                        width: 68px;
                        background-color: #ea4a36;
                        border: none;
                        color: #fff;
                        float: left;
                        cursor: pointer;

                        &:focus {
                            outline: none;
                        }
                    }
                }
            }
        }
    }
</style>