import { requestUserAddressList, requsetOrderInfo } from "@/api"

const actions = {
    async getUserAddressList({commit}){
        let result = await requestUserAddressList()
        if(result.code == 200){
            commit('GETUSERADDRESSLIST',result.data)
        }
        console.log(result)
    },
    async getOrderInfo({commit}){
        let result =await requsetOrderInfo()
        if(result.code == 200){
            commit('GETORDERINFO',result.data)
        }
        console.log(result)
    }
}
const mutations = {
    GETUSERADDRESSLIST(state,value){
        state.addressList = value
    },
    GETORDERINFO(state,value){
        state.orderInfo = value
    }
}
const state = {
    addressList:{},
    orderInfo:{}
}
const getters = {}
export default {
    actions,
    mutations,
    state,
    getters,
}