<template>
  <div class="spec-preview">
    <img :src="imgObj.imgUrl" />
    <div class="event" @mousemove="move"></div>
    <div class="big">
      <img :src="imgObj.imgUrl" ref="big"/>
    </div>
    <div class="mask" ref="mask"></div>
  </div>
</template>

<script>
  export default {
    name: "Zoom",
    props:['skuImageList'],
    data() {
      return {
        currentIndex:0
      }
    },
    computed:{
      imgObj(){
        return this.skuImageList[this.currentIndex]||{}
      }
    },
    mounted(){
      // 全局事件总线 接收数据的组件绑定事件 发送数据的组件触发时间 
        this.$bus.$on('sendIndex',(index)=>{
            this.currentIndex = index
        })
    },
    methods:{
      // offsetX 在盒子内的x坐标 clientX 在页面内的x坐标 offsetX在盒子内当前的x坐标
      /* 
        放大镜实现思路
        1.获取遮罩和放大效果的dom
        2.计算遮罩和放大区域的坐标，
        3.注意：放大区域要乘遮罩的负数倍数，因为是效果类似于镜像，如果两个都是同方向移动的话，放大区域的图片会走出盒子变成空白
      */
      move(event){  
        let mask = this.$refs.mask
        let big = this.$refs.big
        let left = event.offsetX - mask.offsetWidth / 2
        let top = event.offsetY - mask.offsetHeight / 2
        if(left < 0){
          left = 0
        }
        if(left >mask.offsetWidth){
          left = mask.offsetWidth

        }
        if(top < 0){
          top = 0
        }
        if(top >mask.offsetHeight){
          top = mask.offsetHeight
        }
        mask.style.left = left + 'px'
        mask.style.top = top + 'px'
        big.style.left = -2 * left + 'px'
        big.style.top = -2 * top + 'px'
      }
    }
  }
</script>

<style lang="less">
  .spec-preview {
    position: relative;
    width: 400px;
    height: 400px;
    border: 1px solid #ccc;

    img {
      width: 100%;
      height: 100%;
    }

    .event {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 998;
    }

    .mask {
      width: 50%;
      height: 50%;
      background-color: rgba(0, 255, 0, 0.3);
      position: absolute;
      left: 0;
      top: 0;
      display: none;
    }

    .big {
      width: 100%;
      height: 100%;
      position: absolute;
      top: -1px;
      left: 100%;
      border: 1px solid #aaa;
      overflow: hidden;
      z-index: 998;
      display: none;
      background: white;

      img {
        width: 200%;
        max-width: 200%;
        height: 200%;
        position: absolute;
        left: 0;
        top: 0;
      }
    }

    .event:hover~.mask,
    .event:hover~.big {
      display: block;
    }
  }
</style>